  margin-left: 7px;
  margin-right: 10px;
}
.editable-hide {
  display: none !important;
}
.editable-click:hover {
  text-decoration: underline;
}
.editable-empty,
.editable-empty:hover,
.editable-empty:focus,
a.editable-empty,
a.editable-empty:hover,
a.editable-empty:focus {
  font-style: italic;
  color: #D14;
  text-decoration: none;
}
.popover-wrapper a {
  display: inline !important;
}
.popover-wrapper {
  display: inline;
  position: relative;
}
.popover-wrapper form {
  position: absolute;
  top: -53px;
  background: #FFF;
  border: 1px solid #AAA;
  border-radius: 5px;
  padding: 7px;
  width: auto;
  display: inline-block;
  left: 50%;
  margin-left: -110px;
  z-index: 101;
}
.popover-wrapper form:before {
  content: "";
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-top: 10px solid #AAA;
  position: absolute;
  bottom: -10px;
  left: 100px;
}
.popover-wrapper form:after {
  content: "";
  width: 0;
  height: 0;
  border-left: 9px solid transparent;
  border-right: 9px solid transparent;
  border-top: 9px solid #FFF;
  position: absolute;
  bottom: -9px;
  left: 101px;
}
body,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: Gotham!important;
  font-weight: 500!important;
}
.nav,
.pagination,
.carousel,
.panel-title a {
  cursor: pointer;
}
.container.no-pad {
  padding: 0;
}
.no-margin-v {
  margin-top: 0;
  margin-bottom: 0;
}
.navbar-nav > li > .dropdown-menu {
  width: 100%;
}
.row h2 {
  margin: 0 auto;
}
.row.pad-top {
  margin-top: 15px;
}
.table-bordered.joined {
  border-top: 0;
}
.alert {
  margin: 5px 10px;
  border-right: 0;
}
.dropdown-menu {
  left: 0!important;
  right: auto!important;
}
.input-group-addon {
  min-width: 100px;
}
.input-group {
  width: 100%;
}
.pagination {
  margin: 2px auto 0 auto;
}
.progress {
